<template><section><h1>Checkbox</h1>
<template>
    <coding :code="disabled" title="禁止" content="可以禁止checkbox的默认事件" lang="html">
        <y-checkbox v-model="test" label="523" :disabled="disableds">disabled</y-checkbox>
        <y-checkbox v-model="test" label="1" :disabled="disableds">disabled</y-checkbox>
        <y-button @click.native="disableds = !disableds">disabled</y-button>
    </coding>
    <coding :code="label" title="label" content="可以指定checkbox的label" lang="html">
        <y-checkbox v-model="test1" true-label="可用" false-label="不可用"></y-checkbox>
    </coding>
    <coding :code="group" title="checkbox-group" content="多个checkbox可以使用checkbox-group" lang="html">
        <y-checkbox-group v-model="test3">
    	    <y-checkbox label="523">asd</y-checkbox>
    	    <y-checkbox label="2351" content="">asd</y-checkbox>
        </y-checkbox-group>
        {{test3}}
    </coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>slot</td>
<td>文字</td>
<td>slot</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>label</td>
<td>value</td>
<td><code>String</code>,<code>Number</code>,<code>Boolean</code></td>
<td>Boolean</td>
<td>-</td>
</tr>
<tr>
<td>trueLabel</td>
<td>选中value</td>
<td><code>String</code>,<code>Number</code>,<code>Boolean</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>falseLabel</td>
<td>未选中value</td>
<td><code>String</code>,<code>Number</code>,<code>Boolean</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disable</td>
<td>禁用状态</td>
<td>-</td>
<td>false</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>不显示value</td>
<td><code>Boolean</code></td>
<td>false</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
let disabled =
`<template>
    <y-checkbox v-model="test1" true-label="可用" false-label="不可用"></y-checkbox>
</template>
`
let label =
`<template>
    <y-checkbox v-model="test" label="523" :disabled="true">一个小小的checkbox</y-checkbox>
    <y-checkbox v-model="test" label="1" :disabled="true">一个小小的checkbox</y-checkbox>
</template>
`
let group =
`<temlate>
    <y-checkbox-group v-model="test3">
        <y-checkbox label="523">asd</y-checkbox>
        <y-checkbox label="2351" content>asd</y-checkbox>
    </y-checkbox-group>
</template>
`
export default {
	data(){
		return {
			test:["523"],
			test1:"可用",
			test2:"",
			test3:['523'],
            disabled:disabled,
            disableds: false,
            label:label,
            group:group
		}
	}
}
</script>


